
.heading {
  margin-bottom: 0.2rem;
}

.select {
  overflow-y: auto;
  overflow-x: auto;
  color: $controlsColor;
  background-color: $controlsBackgroundColor
}

.select > ul {
  margin: 0rem;
  padding: 0rem;
  list-style: none;
}

.select > ul > li {
  cursor: pointer;
  padding: 0.3rem;
  border-bottom: 0.1rem solid $controlsBorderColor;
}

.select > ul > li:hover {
  background-color: $secondaryHoverBackgroundColor;
  color: $primaryHoverColor;
}

.selectInput {
  cursor: pointer;
  font-size: 0.9rem;
  background-color: $controlsBackgroundColor;
  border-bottom: solid 0.2rem $controlsBorderColor;
  transition: border 0.3s;
  padding: 0.5rem;
  width: 100%;
  display: flex;
  color: $controlsColor;
  margin-top: 2px;
}

.selectInput:hover, .selectInput:focus, .selectInput:active {
  border-bottom: solid 0.2rem $primarySelectionBackgroundColor;
}

.selectInput > i {
  margin-top: 0.1rem;
}

.selectInput > i:hover {
  color: $primarySelectionBackgroundColor;
}

.selectInput > span {
  margin-left: 0.5rem;
  white-space: nowrap;
  overflow-x: hidden;
}

.selectInput > span.link:hover {
  margin-left: 0.5rem;
  color: $primarySelectionBackgroundColor;
  text-decoration: underline;
}
