
.tabPane {
  background-color: $tabPaneBackgroundColor;
}

.tabTitles {
  display: flex;
  flex-wrap: wrap;
  background-color: $tabTitlesBackgroundColor;
  border-bottom: 0.2rem $primaryHoverBackgroundColor solid;
  padding-top: 0.5rem;
  min-height: 2.4rem;
}

.tabContainer {
  display: inline-block;
}

.tabTitle {
  position: relative;
  flex: 0 1;
  /*flexbox workaround some browsers (mostly Safari) needs to have some properties defined explicitly to handle auto sizing correctly*/
  flex-basis: auto;
  flex-shrink: 0;
  /*end flexbox workaround*/
  min-height: 1.7rem;
  white-space: nowrap;
  display:flex;
  background-color: $tabTitleBackgroundColor;
  padding: 0.3rem 0.3rem 0.3rem 0.9rem;
  border-right: 1px $tabTitleBorderColor solid;
  cursor: pointer;
  font-size: 0.91rem;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.tabTitle.active {
  background: $primarySelectionBackgroundColor;
  color: $primarySelectionColor;
}

.tabTitle:hover {
  background: $primaryHoverBackgroundColor;
  color: $primaryHoverColor;
}

.tabMainTitle {
  vertical-align: middle;
}

.tabComplementTitle {
  display: inline-block;
  max-width: 70px;
  overflow: hidden;
  text-overflow: ellipsis;
  vertical-align: middle;
  opacity: 0.5;
  font-size: 0.8rem;
}

.tabClose {
  display: inline-block;
  margin-left: 0.3rem;
}

.tabClose:after {
  content: "\f00d";
  color: $secondaryColor;
  padding: 0.1rem;
  font-family: FontAwesome;
  font-size: 0.9rem;
}

.tabClose:hover {
  background-color: $secondaryHoverBackgroundColor;
}

.contextMenu {
  background-color: $contextMenuBackgroundColor;
  position: absolute;
  padding: 0.5rem;
  min-width: 9rem;
  z-index: 200;
}

.contextMenuContainer {
  position: absolute;
  bottom: 2px;
  left: 2px;
  z-index: 200;
}

.contextButton {
  color: $contextMenuButtonColor;
  padding: 0.5rem;
  font-size: 0.8rem;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.contextButton:hover {
  background-color: $contextMenuButtonHoverBackgroundColor;
  color: $contextMenuButtonHoverColor;
}
